import { createWebHistory, createRouter } from "vue-router";
import Page1 from './Page1.vue';
import Page2 from './Page2.vue';
import Page3 from './Page3.vue';
import Page4 from './Page4.vue';
import Login from './Login.vue';
import Page5 from './Page5.vue';
import Page6 from './Page6.vue';
import Page7 from './Page7.vue';
import Page8 from './Page8.vue';
import Page9 from './Page9.vue';
import Page10 from './Page10.vue';
import Page11 from './Page11.vue';
import Page12 from './Page12.vue';
import Page13 from './Page13.vue';
import Page14 from './Page14.vue';
import Page15 from './Page15.vue';



const routes = [
  {
    path: "/",
    component: Page1
  },
  {
    path: "/Page2",
    component: Page2
  },
  {
    path: "/Page3",
    component: Page3
  },
  {
    path: "/Page4",
    component: Page4
  },
  {
    path: "/Login",
    component: Login
  },
  {
    path: "/Page5",
    component: Page5
  },
  {
    path: "/Page6",
    component: Page6
  },
  {
    path: "/Page7",
    component: Page7
  },
  {
    path: "/Page8",
    component: Page8
  },
  {
    path: "/Page9",
    component: Page9
  },
  {
    path: "/Page10",
    component: Page10
  },
  {
    path: "/Page11",
    component: Page11
  },
  {
    path: "/Page12",
    component: Page12
  },
  {
    path: "/Page13",
    component: Page13
  },
  {
    path: "/Page14",
    component: Page14
  },
  {
    path: "/Page15",
    component: Page15
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;